'use client';

import { useState, useEffect } from 'react';
import axios from 'axios';
import "../add_product/add_product.css";
export default function AddProductPage() {
  const [name, setName] = useState('');
  const [price, setPrice] = useState('');
  const [discountPrice, setDiscountPrice] = useState('');
  const [images, setImages] = useState<string[]>([]);
  const [category, setCategory] = useState('');
  const [description, setDescription] = useState('');
  const [ingredients, setIngredients] = useState<string[]>([]);
  const [instructions, setInstructions] = useState<string[]>([]);
  const [specials, setSpecials] = useState<string[]>([]);
  const [stock, setStock] = useState('');
  const [allCategories, setAllCategories] = useState([]);

  useEffect(() => {
    axios.get('https://api-zeal.onrender.com/api/categories')
      .then(res => setAllCategories(res.data))
      .catch(err => console.error('L·ªói l·∫•y danh m·ª•c:', err));
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const productData = {
      name,
      price: Number(price),
      discountPrice: discountPrice ? Number(discountPrice) : null,
      images,
      category,
      description,
      ingredients,
      usage_instructions: instructions,
      special: specials,
      stock: Number(stock),
    };

    try {
      await axios.post('https://api-zeal.onrender.com/api/products', productData);
      alert('‚úÖ Th√™m s·∫£n ph·∫©m th√†nh c√¥ng!');
    } catch (error: any) {
      alert(`‚ùå Th√™m th·∫•t b·∫°i: ${error.response?.data?.message || 'L·ªói kh√¥ng x√°c ƒë·ªãnh'}`);
    }
  };

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files) {
      const fileNames = Array.from(files).map(file => file.name);
      setImages(prev => [...prev, ...fileNames]);
    }
  };

  return (
    <div className="max-w-5xl mx-auto p-6">
      <h1 className="text-3xl font-bold mb-6 text-center">üõí Th√™m s·∫£n ph·∫©m</h1>
      <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-6">
        
        <div>
          <label className="font-medium">T√™n s·∫£n ph·∫©m</label>
          <input className="input" value={name} onChange={e => setName(e.target.value)} required />
        </div>

        <div>
          <label className="font-medium">Gi√°</label>
          <input type="number" className="input" value={price} onChange={e => setPrice(e.target.value)} required />
        </div>

        <div>
          <label className="font-medium">Gi√° khuy·∫øn m√£i</label>
          <input type="number" className="input" value={discountPrice} onChange={e => setDiscountPrice(e.target.value)} />
        </div>

        <div>
          <label className="font-medium">T·ªìn kho</label>
          <input type="number" className="input" value={stock} onChange={e => setStock(e.target.value)} required />
        </div>

        <div className="md:col-span-2">
          <label className="font-medium">Danh m·ª•c</label>
          <select className="input" value={category} onChange={e => setCategory(e.target.value)} required>
            <option value="">-- Ch·ªçn danh m·ª•c --</option>
            {allCategories.map((c: any) => (
              <option key={c._id} value={c._id}>{c.name}</option>
            ))}
          </select>
        </div>

        <div className="md:col-span-2">
          <label className="font-medium">M√¥ t·∫£</label>
          <textarea className="input" rows={3} value={description} onChange={e => setDescription(e.target.value)} required />
        </div>

        <div>
          <label className="font-medium">Th√†nh ph·∫ßn</label>
          <textarea className="input" rows={3} placeholder="M·ªói th√†nh ph·∫ßn 1 d√≤ng" value={ingredients.join('\n')} onChange={e => setIngredients(e.target.value.split('\n'))} />
        </div>

        <div>
          <label className="font-medium">H∆∞·ªõng d·∫´n s·ª≠ d·ª•ng</label>
          <textarea className="input" rows={3} placeholder="M·ªói d√≤ng l√† m·ªôt b∆∞·ªõc" value={instructions.join('\n')} onChange={e => setInstructions(e.target.value.split('\n'))} />
        </div>

        <div className="md:col-span-2">
          <label className="font-medium">ƒê·∫∑c ƒëi·ªÉm n·ªïi b·∫≠t</label>
          <textarea className="input" rows={2} value={specials.join('\n')} onChange={e => setSpecials(e.target.value.split('\n'))} />
        </div>

        <div className="md:col-span-2">
          <label className="font-medium">T·∫£i ·∫£nh (t√™n file)</label>
          <input type="file" multiple className="input" onChange={handleImageUpload} />
          <div className="flex flex-wrap mt-2 gap-2 text-sm text-gray-600">
            {images.map((img, index) => (
              <span key={index} className="bg-gray-100 px-2 py-1 rounded">{img}</span>
            ))}
          </div>
        </div>

        <div className="md:col-span-2 flex justify-end">
          <button type="submit" className="px-6 py-2 bg-blue-600 text-white rounded hover:bg-blue-700">
            ‚ûï Th√™m s·∫£n ph·∫©m
          </button>
        </div>
      </form>
    </div>
  );
}
